#contactPage {
	width: 100vw;
	max-width: 60rem;
	margin: 0 auto;
	text-align: center;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.contactPageTitle {
	font-size: 3rem;
	margin-bottom: 1rem;
}

.ClientDesc {
	font-weight: 300;
	font-size: 1rem;
}

.clientImgs {
	margin: 1rem 0;
	display: flex;
	flex-wrap: wrap;
}

.clientImg {
	object-fit: cover;
	width: 23%;
	min-width: 9rem;
	margin: auto;
	padding: 0.25rem;
}

#clients {
	padding: 2rem;
	display: none;
}

.links {
	display: flex;
	flex-wrap: wrap;
}

.link {
	object-fit: cover;
	height: 3rem;
	width: 3rem;
	margin: 1rem 1rem;
}
