#skills {
	overflow: hidden;
	width: 100vw;
	max-width: 65rem;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
	margin: 0 auto;
}

.skillTitle {
	font-size: 3rem;
	font-weight: 600;
	padding-top: 3rem;
	margin-bottom: 1.5rem;
}

.skillDesc {
	font-weight: 400;
	font-size: 1rem;
	max-width: 51rem;
	padding: 0 2rem;
}

.skillBars {
	margin: 1.5rem;
	width: 100vw;
	max-width: 80%;
	text-align: left;
}

.skillBar {
	display: flex;
	margin: 1rem;
	padding: 1.5rem 2rem;
	border-radius: 0.5rem;
	background: rgb(50, 50, 50);
}

.skillBarImg {
	object-fit: cover;
	height: 3rem;
	width: 3rem;
	margin-right: 2rem;
}

.skillBarText > p {
	font-size: 0.9rem;
	font-weight: 100;
}

#Software {
	width: 100vw;
	max-width: 50rem;
	margin: 0 auto;
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
}

.SoftwareImg {
	height: 5rem;
	width: 5rem;
}

@media screen and (max-width: 720px) {
	.skillBarText > p {
		font-size: 2vw;
	}
}

@media screen and (max-width: 480px) {
	.skillBarText > p {
		font-size: 3vw;
	}
	.skillBarImg {
		height: 2.25rem;
		width: 2.25rem;
	}
	.skillBarText > h2 {
		font-size: 5vw;
	}
	.SoftwareImg {
		height: 2.5rem;
		width: 2.5rem;
	}
}
